import { useEffect, useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from 'react-router-dom';
import Price from './Price.js'
import getStatus from './getStatus.js';

function Productinfo({product}){
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    useEffect(() => {
        handleSelect();
    }, []);    
    return (
        <div
            className="product-modal"
            role="dialog"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-5">
                                <h2 className="section-heading text-uppercase">product</h2>
                                <h3>No.{product.num} <br/>{product.name}</h3>
                                    <Carousel activeIndex={index} onSelect={handleSelect} indicators={true}>
                                        { product.imgSrc && product.imgSrc.map((img) => (
                                            <Carousel.Item key={img}>
                                                <img
                                                    className="d-block w-100"
                                                    src={img}
                                                    alt={img}
                                                />
                                            </Carousel.Item>
                                        ))
                                        }
                                    </Carousel>
                                    <table border="solid">
                                        <caption>*대여제품 특성상, 상기 이미지와 다를 수 있습니다.(스크래치 등)</caption>
                                        <tbody>
                                            <tr>
                                                <th>캐리어번호</th>
                                                <td>{product.num}</td>
                                            </tr>
                                            <tr>
                                                <th>캐리어이름</th>
                                                <td>{product.name}</td>
                                            </tr>
                                            <tr>
                                                <th>대여상태</th>
                                                <td><strong>{getStatus(product.reserv_date, product.return_date, product.repair, product.damage)}</strong></td>
                                            </tr>
                                            <tr>
                                                <th>사이즈</th>
                                                <td>{product.type}/{product.size}인치</td>
                                            </tr>
                                            <tr>
                                                <th>오픈타입</th>
                                                <td>{product.zip ? "지퍼" : "원터치"}</td>
                                            </tr>
                                            <tr>
                                                <th>확장여부</th>
                                                <td>{product.extension ? "가능" : "불가"}</td>
                                            </tr>
                                            <tr>
                                                <th>바퀴갯수</th>
                                                <td>{product.wheels}개</td>
                                            </tr>
                                            <tr>
                                                <th>사이드핸들</th>
                                                <td>{product.sidehandle ? "있음" : "없음"}</td>
                                            </tr>
                                            {
                                                function(){
                                                    if(product.return_date === null || product.repair || product.damage){
                                                        return null;
                                                    } else {
                                                        return (
                                                            <tr>
                                                                <th>반납예정일</th>
                                                                <td>{product.return_date}</td>
                                                            </tr>
                                                        )
                                                    }
                                                }()
                                            }
                                            {product.desc && 
                                                <tr>
                                                    <th>특징</th>
                                                    <td>{product.desc}</td>
                                                </tr>
                                            }
                                        </tbody>
                                    </table>
                                    {(function(){if(product.type === "미니캐리어"){
                                        return (<p>*미니캐리어는 개별대여가 불가하며 일반캐리어 대여 시 추가가능합니다.(+2,000원)</p>)
                                    } else if(product.repair || product.damage){
                                        return (<p>*현재 대여가 불가한 제품입니다.</p>)
                                    } else {
                                        return (
                                            <Link
                                                to={process.env.PUBLIC_URL + `/product-order/${product.id}`}
                                            >
                                                <button
                                                    className="btn btn-primary btn-xl text-uppercase"
                                                    type="button"
                                                >
                                                    주문하기
                                                </button>
                                            </Link>
                                        )
                                    }})()}
                            </div>
                        </div>
                    </div>
                </div>
                <Price />
            </div>
        </div>
    )
}

export default Productinfo;